/* ALL */
.Images_Images__QZNIr {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 6rem;
}

.Images_Wrapper__VfgjG {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #111;
    border-radius: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-bottom: 3rem;
}
.Images_Info__2yQvU {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
}
.Images_Content__3DvHp, .Images_Buttons__2a56Z {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.Images_Heading__sv0xR {
    color: var(--color-purple);
}

/* IMAGES */
.Images_Repository__XcSOc {
    width: 25rem;
}
.Images_Tag__3p9U9 {
    width: 10rem;
}
.Images_Id__IEWRn {
    width: 10rem;
}
.Images_Created__3g-th {
    width: 9.5rem;
}
.Images_Size__22_W4 {
    width: 15rem;
}

.Images_Repository__XcSOc, .Images_Tag__3p9U9, .Images_Id__IEWRn, .Images_Created__3g-th,
.Images_Size__22_W4 {
    overflow: hidden;
    margin-right: 1.25rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Images_Button__3_KRm {
    margin-right: 1.25rem !important;
}

.Images_Headline__33Xlw {
    color: var(--color-purple);
}

.Images_Images__QZNIr h1,
.Images_Images__QZNIr h2 {
    font-weight: 500;
}

.Images_Images__QZNIr h1 {
    font-size: 1.8rem;
    margin-top: 0;
    margin-bottom: 0.8rem;
}

.Images_Images__QZNIr h2 {
    font-size: 1.6rem;
}

.Images_Images__QZNIr p {
    font-size: 1.1rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.Images_DetailTitle__158fJ {
    color: var(--color-purple);
    width: 4.5rem;
}

.Images_Error__2LWPb {
    color: red;
    white-space: pre;
    text-align: left !important;
}

/* desktop 1200 + */
@media (min-width: 1200px) {
    * {
        /* color: red; */
    }
    .Images_Images__QZNIr {
        margin-top: 10rem;
    }

    .Images_Wrapper__VfgjG {
        width: 90%;
        padding: 4.5rem 2rem 5rem 2rem;
    }
    .Images_Info__2yQvU {
        flex-direction: column;
        justify-content: space-around;
        margin-top: 4rem;
    }

    .Images_Images__QZNIr h1 {
        font-size: 2.5rem;
    }

    .Images_Images__QZNIr p {
        margin-bottom: 1rem;
    }

    /* IMAGES */
    .Images_Repository__XcSOc {
        width: 20rem;
    }
    .Images_Tag__3p9U9 {
        width: 5rem;
    }
    .Images_Id__IEWRn {
        width: 8rem;
    }
    .Images_Created__3g-th {
        width: 9.5rem;
    }
    .Images_Size__22_W4 {
        width: 5rem;
    }
}

/* tabletLandscape 900 - 1199 */
/* @media (min-width: 900px) and (max-width: 1199px) { */
@media (max-width: 1199px) {
    * {
        /* color: blue; */
    }
    /* IMAGES */
    .Images_Repository__XcSOc {
        width: 20rem;
    }
    .Images_Tag__3p9U9 {
        width: 4rem;
    }
    .Images_Id__IEWRn {
        width: 7rem;
    }
    .Images_Created__3g-th {
        width: 7rem;
    }
    .Images_Buttons__2a56Z {
        margin-top: 1rem;
        margin-left: 1rem;
    }
    .Images_DetailTitle__158fJ {
        margin-left: 1.5rem !important;
    }
}

/* tabletPortrait 600 - 899 */
/* @media (min-width: 600px) and (max-width: 899px) { */
@media (max-width: 899px) {
    * {
        /* color: purple; */
    }
    /* IMAGES */
    .Images_Repository__XcSOc {
        width: 17rem;
    }
    .Images_Id__IEWRn {
        width: 7rem;
    }
    .Images_Buttons__2a56Z {
        flex-direction: column-reverse;
        /* margin-top: 1rem; */
    }
    .Images_Button__3_KRm {
        margin-top: 0.75rem !important;
    }
    .Images_DetailTitle__158fJ {
        margin-left: 1.5rem !important;
    }
}

/* phone < 600 */
@media (max-width: 599px) {
    * {
        /* color: green; */
    }
    .Images_Repository__XcSOc {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        /* width: auto; */
        word-break: break-word;
        margin-bottom: 1rem;
        margin-right: 0;
    }
    .Images_Size__22_W4 {
        width: 8rem;
    }
    .Images_DetailTitle__158fJ {
        margin-left: 0.2rem !important;
    }
}

